/* stylelint-disable */
.what-we-do {
  margin-top: 136px;
  max-width: 1600px;
  margin-left: auto;
  margin-right: auto;

  &__title {
    font-weight: 900;
    font-size: 48px;
    line-height: 100%;
    text-align: center;
    color: #253757;
  }

  &__cards {
    margin-top: 136px;
    display: flex;
    flex-direction: row;
  }

  .card:first-child {
    margin-left: 227px;
  }

  .card {
    display: flex;
    flex-direction: column;
    width: 264px;
    text-align: left;
    margin-left: 129px;

    &__img {
      width: 160px;
      height: 160px;
      border-radius: 50%;
    }

    &__about {
      margin-top: 64px;
      font-weight: 900;
      font-size: 20px;
      line-height: 140%;
      color: #253757;
    }

    &__description {
      margin-top: 32px;
      font-weight: normal;
      line-height: 150%;
      color: #334563;
      mix-blend-mode: normal;
    }
  }
}

@media (max-width: 1400px) {
  .what-we-do {

    &__cards {
      justify-content: center;
    }

    .card:first-child {
      margin-left: 20px;
    }

    .card {
      margin-left: 40px;
    }
  }
}

@media (max-width: 800px) {

  .what-we-do {
    margin-top: 0;

    &__cards {
      margin-top: 30px;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      margin-left: 0;
    }

    .card:first-child {
      margin-left: 0;
    }

    .card {
      margin-top: 10px;
      margin-left: 0;
      margin-bottom: 30px;

      &__about {
        margin-top: 20px;
      }

      &__description {
        margin-top: 15px;
      }
    }
  }
}

@media (max-width: 640px) {

  .what-we-do {
    margin-top: 40px;

    .card {
      margin-top: 30px;

      &__img {
        width: 264px;
        height: 264px;
      }
    }
  }
}
